import React from "react";
import { Justtrylinks } from "./justtrylinks";
import { Link } from "@reach/router";

const Servicehiglightsection = ({
  content,
  btn = "Talk To us",
  paragraph,
  h5paragraph,
  isImage = false,
  imageSrc,
}) => {
  return (
    <div className="service-highlight p-2">
      <div
        className={`hero-wrapper gap-lg ${
          isImage === false
            ? "item-center"
            : "flex-center md-flex-column justify-content-around"
        }`}
      >
        <div
          className={`${
            isImage === false ? "item-center" : "item-start w-50 md-w-full"
          } gap-lg`}
        >
          <p
            className={`${
              isImage === false
                ? "c-text-center md-c-text-start"
                : "c-text-start"
            } about-h3 font-white`}
          >
            {content}
          </p>
          {paragraph && (
            <p className="about-p-three font-white c-text-center md-c-text-start">
              {paragraph}
            </p>
          )}
          {h5paragraph && (
            <p className="about-h5 font-white c-text-center md-c-text-start">
              {h5paragraph}
            </p>
          )}
          <Link to={Justtrylinks.contactUs} className="roll-button roll-btn">
            <span>{btn}</span>
          </Link>
        </div>
        {isImage && (
          <div>
            <img src={imageSrc} alt={content} className="w-full" />
          </div>
        )}
      </div>
    </div>
  );
};

export default Servicehiglightsection;
